<template>
    <p-card style="width: 600px; margin: auto;">
        <template #title>
            User groups
        </template>
        <template #content>
            <p-data-table :value="groupList">
                <p-column header="ID" field="gId"></p-column>
                <p-column header="Name" field="groupName"></p-column>
                <p-column header="Active" field="active"></p-column>
                <p-column header="" style="width: 150px">
                    <template #body="{data}">
                        <p-button
                            label="Permissions"
                            @click="groupPermissions(data.gId)"
                            class="p-mr-1"
                        />
                        <p-button
                            icon="pi pi-pencil"
                            @click="updateGroup(data.gId)"
                        />
                    </template>
                </p-column>
            </p-data-table>
        </template>
        <template #footer>
            <p-button label="Create New" @click="createNew" />
        </template>
    </p-card>
</template>

<script>
import { onMounted, ref } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
export default {
    setup() {
        const store = useStore();
        const router = useRouter();
        const groupList = ref();

        onMounted(() => {
            store
                .dispatch("maintanance/getUserGroups")
                .then((data) => {
                    groupList.value = data;
                })
                .catch(() => {});
        });

        return {
            groupList,
            createNew: () => {
                router.push("/maintanance/groups/create");
            },
            updateGroup: (id) => {
                router.push(`/maintanance/groups/${id}/update`);
            },
            groupPermissions: (id) => {
                router.push(`/maintanance/groups/${id}/permissions`);
            }
        };
    }
};
</script>
